@import url(https://fonts.googleapis.com/css?family=Audiowide);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.header{background-color:whitesmoke;height:5rem;line-height:5rem;text-align:center;padding:0}.display{margin:0 auto;position:relative;top:50%;transform:translateY(-50%)}.display .title{background:none;border:none;outline:none;font-family:'Audiowide', cursive;font-size:2rem;padding:0px 15px;color:#111;text-align:center;text-shadow:3px 2px 0px rgba(0,0,0,0.2);padding:0 2rem}

.main{background:#7c4e74;background:linear-gradient(261deg, #7c4e74 0%, #534e7c 100%);min-height:100vh;height:100%}.main__containerswitch{padding:1rem 0}.main__switch{display:flex;justify-content:center;font-weight:bold;width:-webkit-max-content;width:-moz-max-content;width:max-content;margin:auto;border:1px solid #a8436e;border-radius:.25rem}.main .nav-link.active{background-color:#a8436e}.nav-link{color:#42d88b}.nav-link:hover{color:#339b65}

.userlist--flex{display:flex;flex-direction:column}.userlist__buttonlist{font-weight:bold}.buttonlist--marge:last-child{margin-bottom:1rem}

.cardP{background-color:inherit;border:none;margin-bottom:1rem !important}.cardP__header{background-color:#A8436E;color:whitesmoke;font-weight:bold;font-size:larger;display:flex;justify-content:space-between}.cardP__header--left{text-align:left}.cardP__header--right{text-align:right}.cardP__header_icons{margin-left:0.5rem;cursor:pointer}.cardP__body{display:flex;justify-content:space-between}.cardP__footer{background-color:#42D88B;font-weight:bold;display:flex;justify-content:space-between}

.result__container{margin-bottom:1rem;padding-left:0;padding-right:0}.result__graph{height:80vw;max-height:50vh}.result__title{font-size:x-large;text-align:center;text-shadow:17px 9px 20px rgba(68,68,68,0.49)}.result__emoji{font-size:4rem;text-align:center}@media (min-width: 800px){.result .list-group{max-width:720px !important;margin:auto}}

